.button-container {
  justify-content: center;
  gap: 40px;
  display: flex;
}

button {
  border: none;
  border-radius: 10px;
  padding: 24px 48px;
  font-size: 24px;
  transition: all .5s ease-in-out;
}

button:hover {
  background-color: #add8e6;
}

